import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    appBarSpacer: theme.mixins.toolbar,
});

class PredictionsPage extends Component{
    render() {
        return(
            <div style={{flex: 1}}>
                <div className={styles.appBarSpacer} style={{marginBottom: 60}}/>
                <Typography>I predict nothing</Typography>
            </div>
        )
    }
}

PredictionsPage.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(PredictionsPage);