import React from 'react';
import PropTypes from 'prop-types';

/* eslint-disable import/extensions, import/no-unresolved, import/no-extraneous-dependencies */
import {
  Checkbox,
  CheckboxGroup,
  Input,
  RadioGroup,
  Row,
  Select,
  File,
  Textarea,
  Form,
} from 'formsy-react-components';
/* eslint-enable */

const Playground = props => {
  const {
    disabledChoice,
    layoutChoice,
    validateOnSubmitChoice,
    validatePristineChoice,
  } = props;

  let myform = null;

  const resetForm = () => {
    console.log('Reset called'); // eslint-disable-line no-console
    const {formsyForm} = myform;
    formsyForm.reset();
  };

  const submitForm = data => {
    console.log(data); // eslint-disable-line no-console
  };

  const radioOptions = [
    {value: 'a', label: 'Option A'},
    {value: 'b', label: 'Option B'},
    {value: 'c', label: 'Option C'},
  ];

  const radioOptionsDisabled = [
    {value: 'a', label: 'Option A'},
    {value: 'b', label: 'Option B', disabled: true},
    {value: 'c', label: 'Option C'},
  ];

  const optionY = {
    value: 'y',
    label: 'Option Y (yellow css class)',
    className: 'yellow',
  };
  optionY['data-note'] = 'This is a data attribute.';

  const selectOptions = [
    {value: 'a', label: 'Option A'},
    {value: 'a', label: 'Option A (again)'},
    {value: 'b', label: 'Option B'},
    {
      value: 'c',
      label: 'Option C',
      title: 'This is a title attribute for Option C',
    },
    {value: 'd', label: 'Option D', disabled: true},
    optionY,
    {value: 'e1', label: 'Option E-1', group: 'Option group E'},
    {value: 'e2', label: 'Option E-2', group: 'Option group E'},
  ];

  const singleSelectOptions = [
    {value: '', label: 'Please select'},
    ...selectOptions,
  ];

  const refCallback = form => {
    myform = form;
  };

  return (
    <Form
      onSubmit={submitForm}
      layout={layoutChoice}
      className="custom-classname-is-rendered"
      validateOnSubmit={validateOnSubmitChoice}
      validatePristine={validatePristineChoice}
      disabled={disabledChoice}
      ref={refCallback}>
      <fieldset>
        <legend>Input types</legend>
        <Input name="secret" value="I'm hidden!" type="hidden" />
        <Input
          name="text1"
          id="artisanCraftedBespokeId"
          value=""
          label="Text"
          type="text"
          placeholder="Here is a text input."
          help="This is a required text input."
          required
        />
        <Input
          name="date[0]"
          value=""
          label="Date"
          type="date"
          placeholder="This is a date input."
          required
        />
        <Input
          name="email"
          value=""
          label="Email"
          type="email"
          autoComplete="off"
          placeholder="This is an email input."
          help="This email field should not autocomplete."
          validations="isEmail"
          validationErrors={{
            isEmail: 'This doesnt look like a valid email address.',
          }}
          required
        />
        <Input
          type="range"
          name="range1"
          label="Range input"
          min={0}
          max={10}
          step={2}
        />
      </fieldset>
      <fieldset>
        <legend>Textarea</legend>
        <Textarea
          rows={3}
          cols={40}
          name="txtArea1"
          label="Textarea"
          placeholder="This field requires 10 characters."
          help="This is some help text for the textarea."
          validations="minLength:10"
          validationErrors={{
            minLength: 'Please provide at least 10 characters.',
          }}
        />
      </fieldset>
    </Form>
  );
};

Playground.propTypes = {
  disabledChoice: PropTypes.bool.isRequired,
  layoutChoice: PropTypes.oneOf(['horizontal', 'vertical', 'elementOnly'])
    .isRequired,
  validateOnSubmitChoice: PropTypes.bool.isRequired,
  validatePristineChoice: PropTypes.bool.isRequired,
};

export default Playground;