import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Calendar from 'react-calendar';
import Card from '@material-ui/core/Card';
import AlarmList from "./AlarmList";
import moment from 'moment'
import axios from "axios";
import TimeKeeper from 'react-timekeeper';
import Divider from '@material-ui/core/Divider';

import Playground from './Form';

let imgUrl = 'https://i.pinimg.com/originals/0d/04/4c/0d044c10cb49a675389bc0e2ff66226b.jpg';

const theme_styles = theme => ({
    appBarSpacer: theme.mixins.toolbar,
});


const styles = {
    card: {
        width: '100%', 
        height: '80%',
        backgroundImage: 'url(' + imgUrl + ')',
        backgroundSize: 'cover',
        overflow: 'hidden',
        justifyContent: 'center'
    },
};

class AlarmsPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            date: new Date(),
            alarms: [],
            time: '2:15 am',
            layout: 'horizontal',
            showingOptions: true,
            validateOnSubmit: false,
            validatePristine: false,
            disabled: false,
        }
        this.handleTimeChange = this.handleTimeChange.bind(this)
    }

    handleChangeOption = (name, value) => {
        const newState = {};
        newState[name] = value;
        this.setState(newState);
      };
    
      handleToggleOptions = () => {
        const {showingOptions} = this.state;
        this.setState({showingOptions: !showingOptions});
      };
    

    componentDidMount(){
        console.log("ComponentDidMount");
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/alarms/"
        axios
          .get(url)
          .then(response => {
              console.log("Response: ", response);
              const newAlarms = response.data.map(a => {
                return {
                  id: a.id,
                  description: a.description,
                  alarm_time: a.alarm_time,
                  volume: a.volume,
                  active: a.active,
                };
              });
    
              const newState = Object.assign({}, this.state, {
                alarms: newAlarms
              });
    
              this.setState(newState);
          })
          .catch(error => console.log(error));
      }
    

    onChange = date => {
        this.setState({ date })
        var date = moment(this.state.date).format('YYYY-MM-DD');
        console.log(date);
        const url = "https://django-env.qgbzkwbhei.us-west-1.elasticbeanstalk.com/alarms/?date=" + date;
        axios
          .get(url)
          .then(response => {
              console.log("Response: ", response);
              const newAlarms = response.data.map(a => {
                return {
                  id: a.id,
                  description: a.description,
                  alarm_time: a.alarm_time,
                  volume: a.volume,
                  active: a.active,
                };
              });
    
              const newState = Object.assign({}, this.state, {
                alarms: newAlarms
              });
    
              this.setState(newState);
          })
          .catch(error => console.log(error));
          console.log(this.state.alarms);
    }

    handleTimeChange = newTime=> {
        console.log(newTime);
        this.setState({ time: newTime.formatted});
    }

    render() {
        const {
            layout,
            validateOnSubmit,
            validatePristine,
            showingOptions,
            disabled,
        } = this.state;

        return(
            <div style={{flex: 1}}>
                <div className={theme_styles.appBarSpacer} style={{marginBottom: 60}}/>
                <div style={{display: 'flex', justifyContent: 'space-around'}}>
                    <div style={{display: 'flex', flexDirection: 'column', width: '40%'}}>
                        <Card style={{display: 'flex', width: '100%', height: '100%'}}>
                                <div style={{display: 'flex', width: '100%', flexDirection: 'column', padding: 20, alignContent: 'center', justifyContent: 'flex-start'}}>
                                    <Typography variant='h5' style={{fontWeight: 'bold', color: '#C0C0C0', alignSelf: 'flex-start', marginTop: 10, marginBottom: 10}}>Add New Alarm</Typography> 
                                    <Divider/>
                                    <div style={{alignSelf: 'center', marginTop: 30}}>
                                        <TimeKeeper
                                            time={this.state.time}
                                            onChange={this.handleTimeChange}
                                            style={{backgroundColor: 'red'}}
                                        />;
                                    </div>
                                        <Playground
                                        layoutChoice={layout}
                                        validateOnSubmitChoice={validateOnSubmit}
                                        validatePristineChoice={validatePristine}
                                        disabledChoice={disabled}
                                        />
                                </div>
                        </Card>
                    </div>
                    <div style={{display: 'flex', flexDirection: 'column', width: '25%'}}>
                        <AlarmList alarms={this.state.alarms}/>
                    </div>
                    <div style={{display: 'flex', flexDirection: 'column', marginLeft: 10, justifyContent: 'flex-start', alignItems: 'center', width:'30%', height: '130vh'}}>
                        <Card style={{width: '100%', marginBottom: 20}}>
                            <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', paddingTop: '18px'}}>
                                <Typography variant='h6' style={{fontWeight: 'bold', color: '#686868'}}>Alarms Time Range</Typography> 
                                <Typography variant='subtitle2' style={{fontStyle: 'light', color: '#888888', marginBottom: 20}}>View Alarms scheduled for Selected Date</Typography>        
                                <div style={{display: 'flex', flexDirection: 'row',justifyContent: 'center', width: '100%', marginBottom: 165}}>
                                    <Calendar
                                        onChange={this.onChange}
                                        value={this.state.date}
                                    />
                                </div>
                            </div>
                        </Card>
                        <Card style={styles.card}>
                            <Typography>Im sick</Typography>
                        </Card>
                    </div>
                </div>
            </div>
        )
    }
}

AlarmsPage.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(AlarmsPage);